<template>
    <div id="publish_article_list" class="dtt-width-50 dtt-center">
        <el-row>
            <el-col class="dtt-border dtt-height-60 dtt-container-flex dtt-padding-left-10">
                <el-button type="primary" @click="publishArticle()">发布新文章</el-button>
            </el-col>
        </el-row>
        <el-row>
            <el-col>
                <el-table :data="datas" stripe :border="true">
                    <el-table-column type="index" label="编号" width="66"></el-table-column>
                    <el-table-column prop="updatedAt" label="时间"></el-table-column>
                    <el-table-column prop="title" label="标题"></el-table-column>
                    <el-table-column fixed="right" label="操作" width="120">
                        <template slot-scope="scope">
                            <el-button type="text" size="small"
                                       @click.native.prevent="askDeleteArticle(scope.$index)">删除</el-button>
                            <el-button type="text" size="small"
                                       @click.native.prevent="updateRow(scope.$index)">编辑</el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </el-col>
            <el-col>

                <el-dialog title="发布文章" :visible.sync="dialogPublishArticleVisible">
                    <publish-article :mode="'add'" v-if="dialogPublishArticleVisible" @hide-dialog="publishFinished($event)"/>
                </el-dialog>

                <!--编辑文章-->
                <el-dialog title="编辑文章" :visible.sync="dialogEditPublishArticleVisible">
                    <publish-article :mode="'edit'" :article="editArticle" v-if="dialogEditPublishArticleVisible" @hide-dialog="editFinished($event)"/>
                </el-dialog>

                <!--删除文章-->
                <el-dialog title="提示" :visible.sync="dialogDeletePublishArticleVisible">
                    <span>确认删除吗?</span>
                    <span slot="footer" class="dialog-footer">
                        <el-button @click="dialogDeletePublishArticleVisible = false">取 消</el-button>
                        <el-button type="primary" @click="deleteRow(deleteIndex)">确 定</el-button>
                    </span>
                </el-dialog>
            </el-col>
        </el-row>
    </div>
</template>

<script>
    import {TABLE_ARTICLE} from '../utils/bmob.tables'
    import PublishArticle from "../components/publish.article";

    export default {
        name: "publish-article-list",
        components: {PublishArticle},
        data(){
            return{
                datas:[],
                dialogEditPublishArticleVisible:false,
                editArticle:{},
                dialogDeletePublishArticleVisible:false,
                deleteIndex:-1,
                dialogPublishArticleVisible:false
            }
        },
        created(){
            this.getPublishArticleList();
        },
        methods:{
            publishFinished($event){
                this.dialogPublishArticleVisible = $event;
                this.getPublishArticleList();
            },
            editFinished($event){
                this.dialogEditPublishArticleVisible = $event;
                this.getPublishArticleList();
            },
            publishArticle(){
                this.dialogPublishArticleVisible = true;
            },
            askDeleteArticle(index){
                this.deleteIndex = index;
                this.dialogDeletePublishArticleVisible = true;
            },
            deleteRow(index){
                console.log(index);
                let id = this.datas[index].objectId;
                const query = this.Bmob.Query(TABLE_ARTICLE);
                query.destroy(id).then(res => {
                    console.log(res);
                    this.$message.success("删除成功");
                    this.dialogDeletePublishArticleVisible = false;
                    this.getPublishArticleList();
                }).catch(err => {
                    console.log(err);
                    this.$message.error(err.error);
                    this.dialogDeletePublishArticleVisible = false;
                })
            },
            updateRow(index){
                console.log(index);
                this.dialogEditPublishArticleVisible = true;
                this.editArticle = this.datas[index];
            },
            getPublishArticleList(){
                const query = this.Bmob.Query(TABLE_ARTICLE);
                let name = this.Bmob.User.current().username;
                query.equalTo("user_name","==",name);
                query.find().then(res=>{
                    console.log(res);
                    this.datas = res;
                });
            }
        }
    }
</script>

<style lang="scss">
    #publish_article_list{

    }
</style>
